import {
  FormHelperText,
  Grid,
  InputLabel,
  Switch,
  TextField,
} from "@material-ui/core";
import { Autocomplete } from "@material-ui/lab";
import { trim } from "lodash";
import ChipInput from "material-ui-chip-input";
import React, { useEffect, useRef, useState } from "react";
import EditorJs from "react-editor-js";
import { useDispatch, useSelector } from "react-redux";
import { Link, useRouteMatch } from "react-router-dom";
import {
  IAppSelectOption,
  ICategory,
  IPostFieldType,
  IPostFormErrors,
  IPostFormValues,
  IPostType,
  IRootState,
} from "../../../interfaces";
import {
  requestAddPost,
  requestCategoryList,
  requestPostDetails,
  requestPostTypes,
} from "../../../redux/actions";
import { RouteURLs } from "../../../routes/routes";
import { getValidationErrors } from "../../../utills/helpers";
import { EDITOR_JS_TOOLS } from "../../../utills/helpers/Editor";
import AppButton from "../../components/Common/AppButton";
import AppDropZone from "../../components/Common/AppDropZone";
import AppSelect from "../../components/Common/AppSelect";
import Loader from "../../components/Common/Loader";
import { AddPostValidation } from "../../validations";
import TinyMCEEditor from "./TinyMCEEditor";

function slugify(string: any) {
  return (
    string
      .toString()
      .trim()
      .toLowerCase()
      .replace(/\s+/g, "-")
      // eslint-disable-next-line
      .replace(/[^\w\-]+/g, "")
      // eslint-disable-next-line
      .replace(/\-\-+/g, "-")
      // eslint-disable-next-line
      .replace(/^-+/, "")
      .replace(/-+$/, "")
  );
}

const AddPostPage = () => {
  const { params }: { params: any } = useRouteMatch();
  const [formValues, setFormValues] = useState<IPostFormValues>({
    title: "",
    slug: "",
    description: {},
    postType: "",
    isActive: false,
    categories: [],
    featuredImage: null,
    tags: [],
    shortDescription: "",
  });
  const [formErrors, setFormErrors] = useState<IPostFormErrors>({
    title: "",
    description: "",
    postType: "",
    isActive: "",
    categories: "",
    featuredImage: "",
    shortDescription: "",
  });
  const [useClasicEditor, setUseClasicEditor] = useState<boolean>(false);

  const editorRef = useRef();
  const [featuredImagePreview, setFeaturedImagePreview] = useState<string>("");
  const dispatch = useDispatch();
  const { postTypeReducer, categoryReducer, postReducer } = useSelector(
    (state: IRootState) => ({
      postTypeReducer: state.postTypeReducer,
      categoryReducer: state.categoryReducer,
      postReducer: state.postReducer,
    })
  );

  useEffect(() => {
    dispatch(requestPostTypes());
    if (params.slug) {
      dispatch(
        requestPostDetails({
          slug: params.slug,
          id: params.id,
        })
      );
    }
    // eslint-disable-next-line
  }, []);
  useEffect(() => {
    if (!postReducer.isDetailsLoading && params.slug) {
      setFormValues(postReducer.editPostDetails);
      setUseClasicEditor(
        typeof postReducer.editPostDetails.description !== "string"
      );
      setFeaturedImagePreview(
        `${process.env.REACT_APP_POST_IMAGE_ENDPOINT}${postReducer.editPostDetails.featuredImage}`
      );
      setTimeout(() => {
        if (editorRef.current) {
          (editorRef.current as any).instance.isReady.then((data: any) =>
            editorRef.current
              ? (editorRef.current as any).changeData(
                  postReducer.editPostDetails.description
                )
              : null
          );
        }
      }, 1000);
    }
    // eslint-disable-next-line
  }, [postReducer.isDetailsLoading]);

  const formValueChange = (e: any) => {
    const name = e.target.name as IPostFieldType,
      value = e.target.value,
      checked = e.target.checked as boolean;
    const newFormValues = Object.assign({}, formValues);
    if (name === "title") {
      newFormValues.slug = slugify(value);
    }
    if (name === "isActive") {
      newFormValues.isActive = checked;
    } else {
      newFormValues[name] = value;
    }
    if (name === "postType") {
      newFormValues.categories = [];
      dispatch(requestCategoryList({ id: value }));
    }
    setFormValues(newFormValues);
    // unset error message
    const newFormErrors = Object.assign({}, formErrors);
    newFormErrors[name] = "";
    setFormErrors(newFormErrors);
  };
  const categoriesChanged = (e: any, newValue: IAppSelectOption[]) => {
    const newFormValues = Object.assign({}, formValues);
    newFormValues.categories = newValue.map((cat) => cat.value);
    setFormValues(newFormValues);
    // unset error message
    const newFormErrors = Object.assign({}, formErrors);
    newFormErrors.categories = "";
    setFormErrors(newFormErrors);
  };

  const saveBlog = (e: any) => {
    e.preventDefault();
    try {
      const { description } = formValues;
      AddPostValidation.validateSync(
        {
          ...formValues,
          description:
            typeof description === "string"
              ? description
              : JSON.stringify(description),
          featuredImage: featuredImagePreview,
        },
        {
          abortEarly: false,
        }
      );
      dispatch(requestAddPost(formValues));
    } catch (error) {
      setFormErrors(getValidationErrors(error as any));
    }
  };
  const onDrop = (acceptedFiles: any[]) => {
    const newFormValues = Object.assign({}, formValues);
    newFormValues.featuredImage = acceptedFiles[0];
    setFormValues(newFormValues);
    acceptedFiles.forEach((file) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        var image: any = new Image();
        if (image) {
          image.src = reader.result;
          image.onload = function () {
            if (image.width < 200 || image.height < 200) {
              setFormErrors({
                ...formErrors,
                featuredImage: "Please upload  image of atleast 200*200",
              });
              setFeaturedImagePreview("");
              return;
            } else {
              setFeaturedImagePreview(e.target?.result as string);
              setFormErrors({ ...formErrors, featuredImage: "" });
            }
          };
        }
      };
      reader.readAsDataURL(file);
    });
  };
  const tagAdded = (chip: string) => {
    const newFormValues = Object.assign({}, formValues);
    newFormValues.tags.push(chip);
    setFormValues(newFormValues);
  };
  const tagDeleted = (chip: string, index: number) => {
    const newFormValues = Object.assign({}, formValues);
    newFormValues.tags.splice(index, 1);
    setFormValues(newFormValues);
  };
  const { isLoading: postTypeLoading, data: postTypes } = postTypeReducer;
  const { isLoading: categoriesLoading, data: categories } = categoryReducer;
  const { isPostAdding } = postReducer;

  const seletableCategories = categories
    .filter(
      (category) =>
        formValues.categories.findIndex((cat) => cat === category._id) === -1
    )
    .map((category: ICategory) => ({
      value: category._id,
      label: category.name,
    }));
  const renderSaveButton = (
    <Grid item sm={12} md={12} lg={12} xs={12} className="text-right">
      <Link
        to={RouteURLs.POSTS}
        style={{
          textDecoration: "none",
        }}
      >
        <AppButton text={"Cancel"} type="button" isLoading={isPostAdding} />
      </Link>
      &nbsp; &nbsp;
      <AppButton
        text={"Save Post"}
        color="primary"
        type="submit"
        isLoading={isPostAdding}
        className="btn-primary"
      />
    </Grid>
  );

  return (
    <>
      {postReducer.isDetailsLoading && params.slug ? (
        <Loader />
      ) : (
        <form onSubmit={saveBlog}>
          <Grid container direction="row" justify="center">
            <Grid item sm={12} md={12} lg={12} xs={12}>
              <Grid container>
                {renderSaveButton}
                <Grid item sm={8} md={8} lg={8} xs={12}>
                  <div className="form-input">
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "space-between",
                      }}
                    >
                      <InputLabel>Description *</InputLabel>
                      {!params.slug && (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "space-evenly",
                            marginRight: 25,
                          }}
                        >
                          <InputLabel>Use Classic Editor</InputLabel>
                          <Switch
                            color="primary"
                            checked={useClasicEditor}
                            onChange={(e) =>
                              setUseClasicEditor(e.target.checked)
                            }
                          />
                        </div>
                      )}
                    </div>
                    {useClasicEditor ? (
                      <div
                        className={`editor-block ${
                          formErrors.description ? "has-error" : ""
                        }`}
                      >
                        <EditorJs
                          id="post-editor-box"
                          className={"editor-block"}
                          data={formValues.description}
                          tools={EDITOR_JS_TOOLS}
                          placeholder="Write your main content here..."
                          onChange={(data: any, newData: any) =>
                            formValueChange({
                              target: { name: "description", value: newData },
                            })
                          }
                          ref={editorRef}
                        />
                      </div>
                    ) : (
                      <div style={{ paddingRight: 25, paddingTop: 20 }}>
                        <TinyMCEEditor
                          onChange={(e) =>
                            formValueChange({
                              target: {
                                name: "description",
                                value: e.target.getContent(),
                              },
                            })
                          }
                          value={trim(postReducer.editPostDetails?.description)}
                        />
                      </div>
                    )}
                    {formErrors.description ? (
                      <FormHelperText error>
                        {formErrors.description}
                      </FormHelperText>
                    ) : null}
                  </div>
                </Grid>

                <Grid item sm={4} md={4} lg={4} xs={12}>
                  <Grid container>
                    <Grid item sm={12} md={12} lg={12} xs={12}>
                      <TextField
                        className={"form-input"}
                        name="title"
                        label="Title *"
                        type="text"
                        variant="outlined"
                        fullWidth
                        error={!!formErrors.title}
                        helperText={formErrors.title}
                        onChange={formValueChange}
                        value={formValues.title}
                      />
                      <TextField
                        className={"form-input"}
                        name="slug"
                        label="Slug *"
                        type="text"
                        variant="outlined"
                        disabled={
                          formValues._id || slugify(formValues.title)
                            ? true
                            : false
                        }
                        fullWidth
                        error={!!formErrors.slug}
                        helperText={formErrors.slug}
                        onChange={formValueChange}
                        value={formValues.slug}
                      />
                      <AppSelect
                        disabled={postTypeLoading}
                        label={"Post Type *"}
                        options={postTypes.map((postType: IPostType) => ({
                          value: postType._id,
                          label: postType.name,
                        }))}
                        name={"postType"}
                        value={formValues.postType}
                        onChange={formValueChange}
                        error={!!formErrors.postType}
                        helperText={formErrors.postType}
                      />

                      <Autocomplete
                        multiple
                        id="tags-outlined"
                        options={seletableCategories}
                        getOptionLabel={(option) => option.label}
                        filterSelectedOptions
                        value={
                          categoriesLoading
                            ? []
                            : formValues.categories.map((category: string) => {
                                const ind = categories.findIndex(
                                  (cat) => cat._id === category
                                );
                                return {
                                  label: categories[ind].name,
                                  value: categories[ind]._id,
                                };
                              })
                        }
                        onChange={categoriesChanged}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            className={"form-input"}
                            variant="outlined"
                            label="Categories *"
                            placeholder="Select Category"
                            error={!!formErrors.categories}
                            helperText={formErrors.categories}
                          />
                        )}
                        disabled={categoriesLoading}
                      />
                      <AppDropZone
                        onDrop={onDrop}
                        label={"Featured Image *"}
                        message={
                          featuredImagePreview ? (
                            <>
                              <img src={featuredImagePreview} alt="" />
                            </>
                          ) : null
                        }
                        accept="image/*"
                        error={!!formErrors.featuredImage}
                        helperText={formErrors.featuredImage}
                      />
                      <TextField
                        className={"form-input"}
                        name="shortDescription"
                        label="Summary *"
                        multiline
                        rows={6}
                        variant="outlined"
                        fullWidth
                        error={!!formErrors.shortDescription}
                        helperText={formErrors.shortDescription}
                        onChange={formValueChange}
                        value={formValues.shortDescription}
                      />
                      <ChipInput
                        variant="outlined"
                        onAdd={tagAdded}
                        onDelete={tagDeleted}
                        fullWidth
                        label={"Tags"}
                        className={"form-input"}
                        value={formValues.tags}
                      />
                    </Grid>
                  </Grid>
                </Grid>
                {renderSaveButton}
              </Grid>
            </Grid>
          </Grid>
        </form>
      )}
    </>
  );
};

export default AddPostPage;
