import React, { useRef } from "react";
import { Editor } from "@tinymce/tinymce-react";

interface TinyMCEEditorProps {
  onChange: (e: any) => void;
  value: string;
}
const TinyMCEEditor: React.FC<TinyMCEEditorProps> = ({ onChange, value }) => {
  const editorRef = useRef<any>(null);
  return (
    <>
      <Editor
        onInit={(evt, editor) => (editorRef.current = editor)}
        initialValue={value}
        apiKey="lafuzrp8vbrp2kat0vndqtb78sto0fvfg9vjezyuvsu2pa7b"
        textareaName=""
        init={{
          branding: false,
          menubar: "insert format table",
          min_height: 450,
          plugins: [
            "advlist autolink lists link image charmap preview anchor",
            "searchreplace code codesample",
            "insertdatetime media table paste code wordcount quickbars emoticons",
          ],
          mobile: {
            theme: "mobile",
            plugins: [
              "advlist autolink lists link image charmap preview anchor",
              "searchreplace code codesample",
              "insertdatetime media table paste code wordcount quickbars emoticons",
            ],
            toolbar: [
              "undo redo | formatselect fontselect fontsizeselect | bold italic underline strikethrough | forecolor backcolor | alignleft aligncenter alignright alignjustify | link image media | bullist numlist outdent indent | blockquote codesample table emoticons charmap insertdatetime | searchreplace preview code",
            ],
            quickbars_selection_toolbar:
              "bold italic | quicklink h1 h2 h3 blockquote quickimage quicktable",
            toolbar_mode: "wrap",
          },
          toolbar:
            "undo redo | formatselect fontselect fontsizeselect | bold italic underline strikethrough | forecolor backcolor | alignleft aligncenter alignright alignjustify | link image media | bullist numlist outdent indent | blockquote codesample table emoticons charmap insertdatetime | searchreplace preview code",
          content_style:
            "body { font-family:Helvetica,Arial,sans-serif; font-size:14px }",
          toolbar_mode: "wrap",
          quickbars_selection_toolbar:
            "bold italic | quicklink h1 h2 h3 blockquote quickimage quicktable",
        }}
        onChange={onChange}
      />
    </>
  );
};

export default TinyMCEEditor;
